.sfgov-event-card {
  font-family: "Rubik", sans-serif;
  background: #00896d;
  border: 0;
  border-radius: 8px;
  color: #fff;
  display: block;
  padding: 1.1764rem;
  text-decoration: none;
}

.sfgov-event-card .title {
  margin: 1rem 0 0.875rem;
  font-weight: 500;
  font-size: 1.17647rem;
  line-height: 1.58824rem;
  color: #fff;
  margin: 0 0 0.5882rem 0;
}

